import React, { memo, useCallback, useEffect } from "react";
import { LeftModalHeader, ScreenWrapper } from "@true/ui";
import { FlatGrid } from "react-native-super-grid";
import { AppItem } from "./AppItem";
import { APP_ITEM_SIZE } from "./constant";
import { changeStatusBar } from "./utils";
import { useNavigation } from "@true/core";

export default memo(() => {
  const renderItem = useCallback(({item}: {item: string}) => {
    return <AppItem item={item} />
  }, [])

  const navigation = useNavigation();

  useEffect(() => {
    navigation.addListener("focus", () => {
      changeStatusBar('dark')
    })
  }, [])

  return <ScreenWrapper>
    <LeftModalHeader title={"Apps"} hideCloseIcon={true} />
    <FlatGrid
      itemDimension={APP_ITEM_SIZE}
      data={["policy", "qa", "wesign", "inside"]}
      renderItem={renderItem} />
  </ScreenWrapper>
})

