import React, { memo, useCallback } from "react";
import {styled, createReplace} from "@true/core";
import { APP_ITEM_SIZE, APPS } from "./constant";
import {changeStatusBar} from "./utils"

export const AppItem = memo(({item}: {item: string}) => {

  const onPress = useCallback(() => {
    changeStatusBar('dark')
    createReplace(APPS[item].title)()
  }, [item])

  return <SContainer onPress={onPress}>
    <SIcon source={APPS[item].logo}/>
    <SText>
      {APPS[item].title}
    </SText>
  </SContainer>
})

const SContainer = styled.TouchableOpacity`
  justify-content: center;
  align-items: center;
`
const SIcon = styled.Image`
  width: ${APP_ITEM_SIZE}px;
  height: ${APP_ITEM_SIZE}px;
`
const SText = styled.Text`
  
`
